import React from 'react';
import './Image.css';
import apod from './apod.jpg'


const Image = (props) => {
    return (
        <div className="container">
            {props.imgtodisplay &&
                <img src={props.imgtodisplay} className="img-fluid" alt="apod"/>
            }
            {!props.imgtodisplay &&
                <img src={apod} className="img-fluid" alt="apod"/>
            }
        </div>
    )

}

export default Image;