import React from 'react';
import './Description.css';

const Description = (props) => {
    return (
        <div className="container-fluid ">
           {props.desc && 
            <p>{props.desc}</p>
           }
           {!props.desc &&
            <p>Daily Limit Exceeded</p>    
           } 
        </div>
    )
} 

export default Description;