import React from 'react';
import './Navbar.css';
import logo from './favicon-32x32(1).jpg'

const Navbar = (props) => {
    return (
        <div className="navbar">
                <div className="container-fluid">
                    <img src={logo} alt="logo"/>
                    <h2>SpaceIO</h2>
                </div>

        </div>
    )
}

export default Navbar;